@import url(https://fonts.googleapis.com/css?family=Poppins:regular,bold,italic&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Poppins:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1{color:white;font-family:Poppins;font-size:45px;margin-left:40px;flex-basis:450px;display:none}@media (max-width: 510px){h1{margin:0 auto;text-align:center}}.logo__green_letters{color:#459b7a}.logo__image{padding-top:20px;margin-left:40px;display:block;width:200px;flex-basis:200px;height:55px;margin-right:40px}@media (max-width: 393px){.logo__image{margin:0 auto}}

.score__text{color:white;font-family:Poppins;font-size:20px;margin-right:40px;margin-top:35px;display:flex;flex-wrap:nowrap}@media (max-width: 393px){.score__text{margin:35px auto}}@media (max-width: 400px){.score__container{margin:0 auto}}

.links__container{display:flex;justify-content:space-between;height:36px;width:calc(100% - 80px);margin:10px 40px;padding-left:0px;border-radius:16px;background-color:#b9c799;overflow:hidden}@media (max-width: 850px){.links__container{flex-wrap:wrap;height:auto;justify-content:space-around}}@media (max-width: 680px){.links__container{flex-wrap:wrap;height:auto;justify-content:space-around}}.nav__link{display:flex;justify-content:center;align-items:center;background-color:#b9c799;width:100%;height:100%;font-size:16px;color:#424737}@media (max-width: 850px){.nav__link{width:30.5%;padding:8px}}@media (max-width: 680px){.nav__link{width:30%}}@media (max-width: 600px){.nav__link{font-size:14px;width:45.5%;padding:10px}}@media (max-width: 525px){.nav__link{width:100%}}.nav__link-active{background-color:#f5ffde}

.card__container{width:100%;display:flex;background-color:#303030;border-radius:5px;margin-top:30px}@media (max-width: 700px){.card__container{flex-wrap:wrap}}.card__container-second{margin-top:0px}.image__bird{width:150px;height:150px;background-color:#f5ffde;border-radius:10px;display:block;margin:10px;object-fit:cover}@media (max-width: 700px){.image__bird{margin:10px auto}}.name-of-bird{color:white;font-size:30px;margin-top:10px;margin-left:20px;padding-bottom:10px;border-bottom:1px solid #404040}@media (max-width: 700px){.name-of-bird{text-align:center}}.card-info__container{width:100%;margin-right:30px}.rhap_main-controls-button{margin:0 3px;color:#a1ad86;font-size:35px;width:35px;height:35px;outline:none}.rhap_container{background-color:transparent;box-shadow:none;margin-top:-20px}.rhap_time{color:#e6e6e6}.rhap_volume-container{width:100px}@media (max-width: 500px){.rhap_volume-container{width:auto}}.rhap_volume-controls{display:block}.rhap_controls-section{flex:0 1}.rhap_progress-section{margin-left:-16px}@media (max-width: 500px){.rhap_progress-section{margin-left:0px;margin-right:-41px}}.rhap_progress-indicator{background-color:#a1ad86}.rhap_repeat-button{display:none}.rhap_rewind-button{display:none}.rhap_forward-button{display:none}@media (max-width: 500px){.rhap_horizontal-reverse{flex-direction:column}}.rhap_volume-button{color:#a1ad86}.rhap_volume-indicator{background-color:#a1ad86}.species-of-bird{color:white;font-family:"Poppins";border-bottom:1px solid #404040;margin-left:20px;margin-top:-28px}@media (max-width: 700px){.species-of-bird{text-align:center}}.not-species-of-bird{font-family:"Poppins", "Arial";color:white;margin-left:10px}.not-description-of-bird{background-color:#303030;font-family:"Poppins", "Arial";padding:10px;margin-top:-20px;height:65%;color:white}.description-of-bird{background-color:#303030;padding:10px;margin-top:-10px;color:white}.main_card__container{max-width:50%;min-height:303px;background-color:#303030;margin-top:30px;border-radius:5px}@media (max-width: 1120px){.main_card__container{max-width:100%}}.main_card__container-before-click{min-width:50%}@media (max-width: 1120px){.main_card__container-before-click{min-width:100%;width:100px}}.rhap_volume-bar-area{width:45%}

.answer__indicator{display:inline-block;width:10px;height:10px;background-color:#444444;margin-top:5px;margin-right:10px;border-radius:50px}.answer__indicator-wrong{background-color:#c53d2a}.answer__indicator-right{background-color:#54b98f}.options__container{margin-top:30px;margin-right:40px;border-radius:5px;width:100%;max-width:50%}@media (max-width: 1120px){.options__container{max-width:100%;margin-right:5px}}.option{color:#fcf7f0;font-family:Arial}.option__container{display:flex;padding:15.5px;border-bottom:1px solid #404040;cursor:pointer}.option__container:last-child{border-bottom:none}.option__container:hover{background-color:#b8bfa6}.options__list{height:303px;display:flex;flex-direction:column;background-color:#303030;border-radius:5px}

.App {
  height: 100%;
}
body {
  background-color: #222222;
}
.header__container {
  display: flex;
  justify-content: space-between;
}
.game__container {
  display: flex;
  margin: 0px 40px;
  height: 100%;
}
.question__container {
  margin: 0px 40px;
}
.next_level__button {
  width: calc(100% - 80px);
  margin: 20px 40px;
  height: 36px;
  background-color: #303030;
  border: 1px solid #444;
  transition: 0.3s;
  color: white;
  outline: none;
}
.next_level__button-active {
  background-color: #b9c799;
  color: #424737;
  cursor: pointer;
}
.end_game__container {
  background-color: white;
  height: 300px;
  margin: 40px;
  position: relative;
  background-color: #303030;
  border-radius: 5px;
}
.cogratulations {
  font-size: 42px;
  text-align: center;
  color: white;
}
.score_congrats_text {
  margin-top: -29px;
  text-align: center;
  font-size: 20px;
  color: white;
}
.try_again__button {
  width: calc(100% - 80px);
  margin: 40px;
  background-color: #00bc8c;
  border: 1px solid #444;
  outline: none;
  color: white;
}
.max_score_gif {
  display: block;
  margin: 0 auto;
}
@media (max-width: 1120px) {
  .game__container {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (max-width: 700px) {
  .margin_for__button {
    margin-top: 150px;
  }
}
@media (max-width: 500px) {
  .margin_for__button {
    margin-top: 225px;
  }
  .header__container {
    flex-wrap: wrap;
  }
  .cogratulations {
    font-size: 35px;
    padding-top: 15px;
  }
  .score_congrats_text {
    font-size: 16px;
  }
}
@media (max-width: 340px) {
  .max_score_gif {
    width: 217px;
  }
}

